import axios from 'axios';
import React, { useState, useEffect } from 'react';
import {read, utils} from 'xlsx';
import { UsePrevious } from './App';
import { make_cols } from './MakeColumns';
import { SheetJSFT } from './types';
import emailjs from 'emailjs-com';

const ExcelReader = () => {
  const [File, setFile] = useState();
  const [Data, setData] = useState([]);
  const [Cols, setCols] = useState();
  const prevValue = UsePrevious(Data);
  const MINUTE_MS = 30000;



  const FetchComments = async ()=>{
    await axios.get(`https://www.okko.ua/api/uk/exportExcel/gas_stations?`,{responseType: 'blob'},{headers:{'Sec-Fetch-Dest': 'document'}}).then((response) => {
      setFile(response.data);
      handleFile()
    })   
  }

  useEffect(() => {
    const interval = setInterval(() => {
        FetchComments()

    }, MINUTE_MS);

    return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
  }, [])


  const handleChange = (e) => {
    const files = e.target.files;
    if (files && files[0]) setFile(files[0]);
  };

  const handleFile = () => {
    /* Boilerplate to set up FileReader */
    const reader = new FileReader();
    const rABS = !!reader.readAsBinaryString;

    reader.onload = (e) => {
      /* Parse data */
      const bstr = e.target.result;
      const wb = read(bstr, {
        type: rABS ? 'binary' : 'array',
        bookVBA: true,
      });
      /* Get first worksheet */
      const wsname = wb.SheetNames[0];
      const ws = wb.Sheets[wsname];
      /* Convert array of arrays */
      const data = utils.sheet_to_json(ws);
      /* Update state */
      setData(data);
      setCols(make_cols(ws['!ref']));
    };

    if (rABS) {
      reader.readAsArrayBuffer(File);
    } else {
      reader.readAsBinaryString(File);
    }
  };

  useEffect (() => { 
    Data.map((item) => {
       if (item.OKKO === 60112300 || item.OKKO === 60113200 || item.OKKO === 60160900 || item.OKKO === 60113100
        ) {
          if (item.__EMPTY_6.includes('За готівку і банківські картки доступно*:') && (item.__EMPTY_6.includes('ДП:') || item.__EMPTY_6.includes('PULLS Diesel:'))){

            if (prevValue.length > 1) {
              prevValue.map((prevItem) => {
                if (prevItem.OKKO === item.OKKO
                  ) {
                  if (!(prevItem.__EMPTY_6.includes('За готівку і банківські картки доступно*:') && (prevItem.__EMPTY_6.includes('ДП:') || prevItem.__EMPTY_6.includes('PULLS Diesel:')))){
                  //   const templateParams = {
                  //     message: `${item.__EMPTY_1} Є дизпаливо по картках. OKKO`
                  // };
                  //   emailjs.send('service_78bgvog', 'template_rrocfu4', templateParams, 'CIwkzASVn2LRgUqgZ')
                  //   .then((result) => { 
                  //   }, (error) => {
                  //       console.log(error.text);
                  //   });
                  console.log(item.__EMPTY_1);
                  }

                 }
              })
            }
      };
       }
     });
    },[Data])


    return (
      <div>
        <label htmlFor="file">Upload an excel to Process Triggers</label>
        <br />
        <input
          type="file"
          className="form-control"
          id="file"
          accept={SheetJSFT}
          onChange={handleChange}
        />
        <br />
        <input
          type="submit"
          value="Process Triggers"
          onClick={handleFile}
        />
      </div>
    );
}

export default ExcelReader;