import logo from './logo.svg';
import './App.css';
import axios from 'axios';
import { useEffect, useRef, useState } from 'react';
import emailjs from 'emailjs-com';
import ExcelReader from './ExcelSearch';

export const UsePrevious = (value) => {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
};


function App() {
  const [Info, setInfo] = useState([]);
  const prevValue = UsePrevious(Info);
  useEffect(() => {
    const interval = setInterval(() => {
      for (let i = 0; i< quantity.length; i++) {
        FetchComments(quantity[i])
        setInfo(resultArray)
      }
      resultArray = [];
    }, MINUTE_MS);

    return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
  }, [])
  const quantity = [1015,812,868,1046];
  let resultArray = [];

  
  for (let i = 0; i< quantity.length; i++) {
  if(Info[i] && prevValue[i]) 
  { if (prevValue[i].name === Info[i].name) {
     if (!(prevValue[i].workDescription.includes('ДП - Готівка, банк.картки') || prevValue[i].workDescription.includes('МДП+ - Готівка, банк.картки')) && (Info[i].workDescription.includes('ДП - Готівка, банк.картки') || Info[i].workDescription.includes('МДП+ - Готівка, банк.картки')))
     {
       console.log(prevValue[i].name);
      const templateParams = {
        message: `${Info[i].name} WOG Є дизпаливо по картках. ${(Info[i].workDescription.includes('ДП - Готівка, банк.картки'))?'Дп - звичайне':'Дп -мустанг'}`,
    };
      emailjs.send('service_78bgvog', 'template_rrocfu4', templateParams, 'CIwkzASVn2LRgUqgZ')
      .then((result) => { 
      }, (error) => {
          console.log(error.text);
      });
  }}}}
  const MINUTE_MS = 300000;

  
  const FetchComments = async (i)=>{
    await axios.get(`https://api.wog.ua/fuel_stations/${i}`).then((response) => {
      
      const result = JSON.parse(response.request.response)
      resultArray.push(result.data);
    })   
  }


  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
      <ExcelReader/>
    </div>
  );
}

export default App;
